let paso = 1;
const pasoInicial = 1;
const pasoFinal = 3;

const cita = {
    id: '',
    nombre: '',
    fecha: '',
    hora: '',
    servicios: []
};

document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp(){
    mostrarSeccion(); //muestra y oculta las secciones
    tabs(); //cambia la seccion cuando se presiones los tabs
    botonesPaginador(); //agrega o quita los botones del paginadores
    paginaSiguiente();
    paginaAnterior();
    consultarAPI(); //consulta la api en el backen en php
    nombreCliente(); //a単ade el nombre del client al objeto de cita
    idCliente(); //a単ade el id del client al objeto de cita
    seleccionarFecha(); //a単ade la fecha de la cita en el objeto
    seleccionarHora(); //a単ade la hora de la cita en el objeto
    mostrarResumen(); //muestra el resumen de la cita

}

function tabs(){
    const botones = document.querySelectorAll('.tabs button');

    botones.forEach(boton => {
        boton.addEventListener('click', function(e){
            paso = parseInt(e.target.dataset.paso);
            mostrarSeccion();
            
            botonesPaginador();

        })
    })
    
}

function mostrarSeccion(){

    // ocultar la seccion que tenga la clase mostra
    const seccionAnterior = document.querySelector('.mostrar');
    if(seccionAnterior){
        seccionAnterior.classList.remove('mostrar');
    }


    // seleccionar la seccion con el paso...
    const pasoSelector = `#paso-${paso}`;
    const seccion = document.querySelector(pasoSelector);
    seccion.classList.add('mostrar');

    // quita la clase anctual al anterori tab
    const tabAnterior = document.querySelector('.actual');
    if(tabAnterior){
        tabAnterior.classList.remove('actual');
    }

    // resaltar el tab actual
    const tab = document.querySelector(`[data-paso="${paso}"]`);
    tab.classList.add('actual');
}

function botonesPaginador(){

    const paginaAnterior = document.querySelector('#anterior');
    const paginaSiguiente = document.querySelector('#siguiente');

    if(paso === 1){
        paginaAnterior.classList.add('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    } else  if(paso === 3) {
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.add('ocultar');

        mostrarResumen();
    } else {
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }

    mostrarSeccion();

}

function paginaSiguiente(){
    const paginaAnterior = document.querySelector('#anterior');
    paginaAnterior.addEventListener('click', function(){
        if(paso <= pasoInicial) return;

        paso--;
        
        botonesPaginador();
    });
}

function paginaAnterior(){
    const paginasiguiente = document.querySelector('#siguiente');
    paginasiguiente.addEventListener('click', function(){
        if(paso >= pasoFinal) return;

        paso++;
        
        botonesPaginador();
    });
}

async function consultarAPI(){

    try {

        const url = 'http://localhost:3000/api/servicios';
        const resultado = await fetch(url);
        const servicios = await resultado.json();
        mostrarServicios(servicios);

    } catch (error) {
        console.log(error);
    }

}

function mostrarServicios(servicios) {

    servicios.forEach(servicio => {
        const {id, nombre, precio} = servicio;
        
        const nombreServicio = document.createElement('p');
        nombreServicio.classList.add('nombre-servicio');
        nombreServicio.textContent = nombre;

        const precioServicio = document.createElement('P');
        precioServicio.classList.add('precio-servicio');
        precioServicio.textContent = `$${precio}`;

        const servicioDiv = document.createElement('DIV');
        servicioDiv.classList.add('servicio');
        servicioDiv.dataset.idServicio = id;
        servicioDiv.onclick = function(){
            seleccionarServicio(servicio)
        };

        servicioDiv.appendChild(nombreServicio);
        servicioDiv.appendChild(precioServicio);

        document.querySelector('#servicios').appendChild(servicioDiv);
    });
}

function seleccionarServicio(servicio) {
    const { id } = servicio;
    const { servicios } = cita;

    // identificar al elemnto que se da el click
    const divServicio = document.querySelector(`[data-id-servicio="${id}"]`);

    // comprobar si un servicio ya fue agregado o quitarlo
    if(servicios.some(agregado => agregado.id === id)){
        // eliminarlo
        cita.servicios = servicios.filter(agregado => agregado.id !== id);
        divServicio.classList.remove('seleccionado');
    } else {
        // agregarlo
        cita.servicios = [...servicios, servicio];
        divServicio.classList.add('seleccionado');
    }

    console.log(cita);

}

function nombreCliente(){
    const nombre = document.querySelector('#nombre').value;
    
    cita.nombre = nombre;
}

function idCliente(){
    const id = document.querySelector('#id').value;
    
    cita.id = id;
}

function seleccionarFecha(){
    const inputFecha = document.querySelector('#fecha');
    inputFecha.addEventListener('input', function(e){
        const dia = new Date(e.target.value).getUTCDay();

        if([6,0].includes(dia)){
            e.target.value = '';
            mostrarAlerta('Los sabados y domingos no hay servicio', 'error', '.formulario');
        } else {
            cita.fecha = e.target.value;
        }
        
    });

}

function seleccionarHora(){
    const inputHora = document.querySelector('#hora');
    inputHora.addEventListener('input',  function(e){

        const horaCita = e.target.value;
        const hora = horaCita.split(":")[0];
        if(hora < 10 || hora > 18) {
            e.target.value = '';
            mostrarAlerta('Hora no valida','error', '.formulario');
        } else {
            cita.hora = horaCita
        }
    });
}

function mostrarResumen(){
    const resumen = document.querySelector('.contenido-resumen');

    // // limpiar el contenido de resumen
    while(resumen.firstChild){
        resumen.removeChild(resumen.firstChild);
    }

    if(Object.values(cita).includes('') || cita.servicios.length === 0) {
        mostrarAlerta('Faltan datos de servicios, Fecha u Hora', 'error', '.contenido-resumen', false);
        return;
    }

    // formatear el div del resumen
    const { nombre, fecha, hora, servicios } = cita;

    // heading para servicios resumen
    const headingServicios = document.createElement('H3')
    headingServicios.textContent = 'Resumen de Servicios';
    resumen.appendChild(headingServicios);

    // iterando y mostrando servicios
    servicios.forEach(servicio => {
        const { id, nombre, precio } = servicio;
        const contenedorServicio = document.createElement('DIV');
        contenedorServicio.classList.add('contenedor-servicio');

        const textoServicio = document.createElement('P');
        textoServicio.textContent = nombre;

        const precioServicio = document.createElement('P');
        precioServicio.innerHTML = `<span>Precio:</span>$${precio}`;

        contenedorServicio.appendChild(textoServicio);
        contenedorServicio.appendChild(precioServicio);

        resumen.appendChild(contenedorServicio);
    });

    // heading para cita resumen
    const headingCita = document.createElement('H3')
    headingCita.textContent = 'Resumen de Cita';
    resumen.appendChild(headingCita);

    const nombreCliente = document.createElement('P');
    nombreCliente.innerHTML = `<span>Nombre:</span> ${nombre}`;

    // formatear la fecha en espa単ol
    const feachaObj = new Date(fecha);
    const mes = feachaObj.getMonth();
    const dia = feachaObj.getDate() + 2;
    const year = feachaObj.getFullYear();

    const fechaUTC = new Date(Date.UTC(year, mes, dia));

    const opciones = {weekday: 'long', year: 'numeric', month: 'long', day: 'numeric'};
    const fechaFormateada = fechaUTC.toLocaleDateString('es-CO', opciones);

    const fechaCita = document.createElement('P');
    fechaCita.innerHTML = `<span>Fecha:</span> ${fechaFormateada}`;

    const horaCita = document.createElement('P');
    horaCita.innerHTML = `<span>Hora:</span> ${hora} Horas`;

    // boton para crear una cita
    const botonReservar = document.querySelector('BUTTON');
    botonReservar.classList.add('boton');
    botonReservar.textContent = 'Reservar Cita';
    botonReservar.onclick = reservarCita;

    resumen.appendChild(nombreCliente);
    resumen.appendChild(fechaCita);
    resumen.appendChild(horaCita);
    resumen.appendChild(botonReservar)

}

async function reservarCita(){
    
    const {nombre, fecha, hora, servicios, id} = cita;

    const idServicio = servicios.map(servicio => servicio.id);

    const datos = new FormData();
    datos.append('usuarioId', id);
    datos.append('fecha', fecha);
    datos.append('hora', hora);
    datos.append('servicios', idServicio);

    try {
    // peticion hacia la api
    const url = 'http://localhost:3000/api/citas';
    const respuesta = await fetch(url, {
        method: 'POST',
        body: datos
        
    });
    const resultado = await respuesta.json();

    if(resultado.resultado){
        Swal.fire({
            icon: 'success',
            title: 'Cita Creada',
            text: 'Tu cita fue creada correctamente',
            button: 'OK'
        }).then(() => {
            window.location.reload();
        }).catch((err) => {
            console.log(err);
        });;
    }
        
    } catch (error) {
        Swal.fire({
            icon: 'error',
            title: 'Error Cita',
            text: 'Se genero un error al generar la cita',
            button: 'OK'
        })
    }

    // console.log([...datos]);

}

function mostrarAlerta(mensaje, tipo, elemento, desaparece = true) {

    // previene que se generar multiples alertas
    const alertaPrevia  =  document.querySelector('.alerta');
    if(alertaPrevia) {
        alertaPrevia.remove();
    }

    const divMensaje = document.createElement('DIV');
    divMensaje.textContent = mensaje;
    divMensaje.classList.add('alerta');
    divMensaje.classList.add(tipo);

    const formulario = document.querySelector(elemento);
    formulario.appendChild(divMensaje);

    if(desaparece) {
        setTimeout(() => {
            divMensaje.remove();
        },3000)
    }

}
